import React, { createContext, useState, useEffect, useContext } from "react";
import axios from "../api/axios";
import { AuthContext } from "./AuthContext";

export const TimetableContext = createContext();

export const TimetableProvider = ({ children }) => {
  const { token } = useContext(AuthContext);
  const [entries, setEntries] = useState([]);
  const [timetableError, setTimetableError] = useState("");

  useEffect(() => {
    if (token) fetchEntries();
  }, [token]);

  const fetchEntries = async () => {
    try {
      const res = await axios.get("/timetable");
      setEntries(res.data);
      setTimetableError("");
    } catch (e) {
      setTimetableError("Unable to load timetable.");
      setEntries([]);
    }
  };

  const addEntry = async (entry) => {
    try {
      const payload = {
        ...entry,
        dayOfWeek: entry.dayOfWeek || entry.day,
        activityName: entry.activityName || entry.subject,
      };
      delete payload.day; // normalize
      delete payload.subject;
      const res = await axios.post("/timetable", payload);
      setEntries([...entries, res.data]);
      setTimetableError("");
    } catch (e) {
      setTimetableError("Failed to add timetable entry.");
    }
  };

  const updateEntry = async (id, updates) => {
    try {
      const payload = {
        ...updates,
        dayOfWeek: updates.dayOfWeek || updates.day,
        activityName: updates.activityName || updates.subject,
      };
      delete payload.day;
      delete payload.subject;
      const res = await axios.put(`/timetable/${id}`, payload);
      setEntries(entries.map((e) => (e._id === id ? res.data : e)));
      setTimetableError("");
    } catch (e) {
      setTimetableError("Failed to update entry.");
    }
  };

  const deleteEntry = async (id) => {
    try {
      await axios.delete(`/timetable/${id}`);
      setEntries(entries.filter((e) => e._id !== id));
      setTimetableError("");
    } catch (e) {
      setTimetableError("Failed to delete entry.");
    }
  };

  return (
    <TimetableContext.Provider
      value={{ entries, addEntry, updateEntry, deleteEntry, timetableError }}
    >
      {children}
    </TimetableContext.Provider>
  );
};
